
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
* {
  box-sizing: border-box;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
.test:after {clear:both; content:''; display:table;}
.test {
  margin: 2rem 0;
  font: 1rem/1.2 Arial,sans-serif;
  .head h1, .source, .model, .result {
    height: 225px;
    overflow: auto;
    display: inline-block;
    vertical-align: top;
    float: left;
    width: 49%;
    padding: 0 1rem 1rem;
    margin: 0;
    position: relative;
  }
  .head {
    background: #f5f5f5;
    border-bottom: solid 1px #777;
    &:after {
      content: "";
      display: table;
      clear: both;
    }
    // color: #fff;
    h1 {
      font-size: 0.75rem;
      line-height: 1;
      padding: 0.1rem 1rem;
      height: auto;
      margin: 0;
    }
  }
  .source, .model {
    p {
      margin: 0.5rem 0;
    }
  }
  .source {
  }
  .model {
    border-left: solid 1px #777;
  }
  .result {
    border-top: solid 1px #777;
    width: 100%;
    clear: both;
    height: auto;
    // diff
    & > p {
      font-size: 0.5rem;
      margin: 1rem 0;
    }
    .diff {
      display: inline-block;
      border: 0.1em solid #ddd;
      font-size: 0.825rem;
      ol {
        display: table-cell;
        margin: 0;
        padding: 0;
        list-style: none;
        &.count {
          border-color: #ddd;
          border-width: 0 0.1em 0 0;
          border-style: solid;
        }
      }
      li {
        margin: 0;
        padding: 0.4em;
        list-style: none;
        display: block;
        list-style-type: none;
        margin: 0;
        white-space: pre;
        .count & {
          padding: 0.4em 0.5em;
        }
      }
      .data {
        .delete {
          background-color: #ffd8d8;
          em {
            background: #fff8f8;
            border-color: #c44;
            border-style: solid;
            border-width: 0.1em;
            color: #900;
            font-style: normal;
          }
        }
        .insert {
          background-color: #d8ffd8;
          em {
            background: #f8fff8;
            border-color: #090;
            border-style: solid;
            border-width: 0.1em;
            color: #363;
            font-style: normal;
          }
        }
        .equal {
          color: #fff;
          background-color: rgb(10, 99, 56);
        }
      }
      .texttitle {
        display: none;
      }
      .author {
        display: none;
      }
    }
  }
  .description {
    font: 0.75rem Arial,sans-serif;
    padding: 0.5rem 1rem;
    background: #f5f5f5;
    // margin: 1rem 0;
    clear: both;
  }
  .testTable {
    td {
      padding: 0.45rem 1rem
    }
  }
}

.nowrap {
  white-space: nowrap;
}


